.wrapper-forside {
    margin-top: 100px;
    background-color: #312d2b;
    min-height: rem(730);

}

.grid {
    display: grid;
    grid-template-rows: rem(150) rem(150);
    gap: rem(30);
    grid-template-columns: 45% 45%;
    grid-template-areas: "item1 item1 "
        "item2 item2"
        "item3 item4";

    padding: {
        top: rem(120);
        bottom: rem(120);
    }
    
  


    &--item1 {
        grid-area: item1;
    }

    &--item2 {
        grid-area: item2;
    }

    &--item3 {
        grid-area: item3;
    }

    &--item4 {
        grid-area: item4;
    }
}


.card-carousel {
    display: flex;
    height: 800px;
    background-color: #f3f3f3;
}



@include breakpoint(sm) {
    .grid {
        grid-template-rows: rem(245) rem(245);
        gap: rem(30);
        grid-template-columns: 23% 49% 23%;
        grid-template-areas: "item1 item2 item3"
            "item1 item2 item4";
    }
}