.spinner{
  border: 5px solid lightgrey;
  border-top: 5px solid grey;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  animation: spin 1s linear infinite;
  margin: 20px auto;
  @keyframes spin {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
}
.spinner-wrapper{
  margin: {
    top: rem(90);
  }
}