.card-cut:after {
  content: "";
  left: 0;
  top: 0;
  opacity: 0.2;
  clip-path: polygon(100% 0, 100% 93%, 89% 100%, 0 100%, 0 0);
}
.card-cut:hover .plus-minus {
  transition: translate 1s;
  margin-top: 3px;
}
.card-cut:hover .plus-minus--one {
  display: none;
}
.card-cut:hover .card-img-overlay {
  display: flex;
}
.card-cut .badge {
  display: flex;
  justify-content: space-between;
