.zone {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin: {
        top: 120px;
        bottom: rem(280);
    }
        &__wrapper{
            display: flex;
            flex-direction: column;
            justify-content: center;
            width: 60vw;
            height: auto;
            margin{
                left: rem(30);
                right: rem(30);
            };
            
        }
}

@include breakpoint(xs){
    .zone{
        margin-bottom: rem(285);
        &__wrapper {
            justify-content: center;
            flex-direction: row;
        }
    }
}
